/*===== Сброс стилей =====*/
html {
  scroll-behavior: smooth;
}
body {
  font-family: 'Gilroy', sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(5deg, rgba(212, 252, 121, 0.4) 0%, rgba(150, 230, 161, 0.4) 50%, rgba(0, 198, 255, 0.4) 100%);
  background-attachment: fixed;
  color: #333;
  overflow-y: auto;
}


.container {
  display: flex;
  align-items: center;
  padding: 50px;
  max-width: 1200px;
  margin: 0 auto;
}

main {
  padding: 20px;
}


/*===== BUTTONS =====*/
button {
  font-family: 'Gilroy', sans-serif;
  background-color: #009688;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}
button:hover {
  background-color: #a1dbab;
}

/*=== Стиль кнопки ===*/
.btn {
  padding: 12px 20px;
  background-color: #009688;
  margin-top: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.btn:hover {
  background-color: #89c194;
}


/*=== Скрытие элементов ===*/
.hidden {
  display: none;
}


/* Стили для контейнера SVG */
.svg-container {
  margin-top: 20px;
  text-align: center;
}

svg {
  max-width: 100%;
  height: auto;
}
