/*=== FONTS ===*/
@font-face {
    font-family: 'Gilroy';
    src: url('components/fonts/Gilroy/Gilroy-Regular.woff2') format('woff2'),
         url('components/fonts/Gilroy/Gilroy-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Gilroy';
    src: url('components/fonts/Gilroy/Gilroy-Bold.woff2') format('woff2'),
         url('components/fonts/Gilroy/Gilroy-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Gilroy';
    src: url('components/fonts/Gilroy/Gilroy-ExtraBold.ttf') format('truetype');
    font-weight: 800;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Gilroy';
    src: url('components/fonts/Gilroy/Gilroy-Light.otf') format('truetype');
    font-weight: 300;
    font-style: normal;
  }
  