/*===== FOOTER =====*/
footer {
    padding: 20px 0;
}

.footer-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    width: 100%;
}
.footer-navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    width: 100%;
}

/*===== NAVIGATION_Logo =====*/
.footer_nav-icon img {
    width: 120px;
    height: auto;
    border-radius: 30px;
    transition: transform 0.3s ease;
}

.footer_nav-icon img:hover {
    transform: scale(1.1);
}

@media (max-width: 768px) {
    footer {
        display: none;
    }    
}