#scrollToTopBtn {
    position: fixed;
    bottom: 30px;
    left: 30px; 
    z-index: 99;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 15px;
    border-radius: 50%;
    font-size: 28px; 
    opacity: 0.8;
    background: no-repeat center center;
    background-size: contain;
}

#scrollToTopBtn:hover {
    opacity: 1; 
}
#scrollToTopBtn:active {
    opacity: 0.9;
}

/*=== Adaptive ===*/
@media (max-width: 480px) {
    #scrollToTopBtn {
        position: fixed;
        border-radius: 0px;
        bottom: 0;
        left: 0;
        z-index: 101;
        width: 100px;
        height: 100px;
        padding: 8px;
    }
}