/*===== FORM =====*/
.tabs {
    display: flex;
    gap: 2px;
    margin-bottom: 30px;
}
.tab {
    padding: 10px 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #009688;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
.tab.active {
    background-color: #a1dbab;
    color: #fff;
}
.tabs button {
    margin-left: 2px;
}

.forms .form {
    display: none;
}

.forms .form.active {
    display: block;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.form_input input[type="date"],
.form_input input[type="name"],

.form_input input {
    width: 80%;
    padding: 12px 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    transition: border-color 0.3s ease;
}

.form_input input[type="date"]:focus {
    border-color: #a1dbab;
    outline: none;
}

.form_toggle {
    display: flex;
    gap: 16px;
    margin-top: 10px;
}

.form_toggle-item {
    position: relative;
}

.form_toggle-item input {
    display: none;
}

.form_toggle-item label {
    display: block;
    padding: 10px 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.form_toggle-item input:checked + label {
    background-color: #a1dbab;
    color: #fff;
}
.item-1{
    display: flex;
}
.item-1 label {
    padding-top: 10px;
}
.item-2 label {
    padding-top: 10px;
}

/*===== Tabs.css =====*/
@media (max-width: 768px) {
    .tabs {
        flex-direction: column;
    }

    .tab {
        width: 100%;
        margin-bottom: 10px;
    }

    .form_input input {
        width: 100%;
    }
}